@import url(https://fonts.googleapis.com/css2?family=Saira:wght@100;200;300;400;600&display=swap);
.container {
  position: fixed;
  font-family: "Saira", sans-serif;
  font-size: 20px;
  letter-spacing: 4px;
  color: #486781;
}

/* #headShotCol {
  position: relative;
} */

#headShotImg {
  position: relative;
  left: -10%;
  height: -webkit-fill-available;
  max-height: 100vh;
  border-left: 3px solid #486781;
  border-right: 3px solid #486781;
}

.displayFlex {
  display: flex;
}
.justifyContentCenter {
  justify-content: center;
}
#greetingCol {
  /* position: fixed; */
}

#column2 {
  position: relative;
  top: -100%;
  left: -15px;
  width: 100vw;
  height: -webkit-fill-available;
  max-height: 100vh;
  border: 2.5px solid rgba(255, 255, 255, 0.6);
  background-color: #ffffff78;
  box-shadow: 26px 25px 45px 0px #94a6bc57;
}

#greetingLine1,
#greetingLine4 {
  font-size: 20px;
  font-weight: 200;
}

#greetingLine1 {
  margin-top: 10px;
  padding-left: 15%;
}

#greetingLine2 {
  padding-left: 20%;
  font-weight: 100;
}

#greetingLine3 {
  font-size: 30px;
  font-weight: 300;
  letter-spacing: 12px;
}

#banner {
  margin-top: 70%;
  padding: 10px;
  background-color: #d7b22ae0;
  box-shadow: 7px 8px 15px 0px #94a6be99;
  width: 101%;
  color: white;
}

#bannerRow1,
#bannerRow2 {
  width: 100%;
  text-align: center;
}

#bannerRow1 {
  font-weight: 200;
}
#bannerRow2 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 5px;
}

#btnResume {
  margin: 10px 0 0;
  padding: 10px 30px;
  border-color: #94a6bb;
  background-color: #486781;
  box-shadow: 7px 8px 15px 0px #94a6be99;
  color: white;
}

.icon {
  margin: 10px 0;
  width: 50px;
  height: 50px;
  -webkit-filter: drop-shadow(0.5px 0.5px 0.25px rgba(255, 255, 255, 0.8))
    drop-shadow(1.5px -0.5px 0px rgba(255, 255, 255, 0.8))
    drop-shadow(0.5px 1px 0.5px rgba(255, 255, 255, 0.8))
    drop-shadow(-1px 0.5px 0px rgba(255, 255, 255, 0.8));
          filter: drop-shadow(0.5px 0.5px 0.25px rgba(255, 255, 255, 0.8))
    drop-shadow(1.5px -0.5px 0px rgba(255, 255, 255, 0.8))
    drop-shadow(0.5px 1px 0.5px rgba(255, 255, 255, 0.8))
    drop-shadow(-1px 0.5px 0px rgba(255, 255, 255, 0.8));
}

#finalLine {
  /* margin: 20px 0;  */
}

#finalLine div {
  /* padding: 5px 10px 15px; */
  /* margin-top: 40px; */
  font-weight: 300;
  text-align: center;
}

@media (min-width: 576px) {
  #headShotCol {
    /* left: -10%; */
  }
  #headShotImg {
  }
  #column2 {
    width: 108%;
  }

  #banner {
    margin-top: 55%;
  }

  /* #column2 {
    position: absolute;
    top: 0;
    left: 0;
  } */
}

@media (min-width: 768px) {
  .container {
    font-size: 20px;
    position: unset;
  }
  #headShotCol {
    position: unset;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  #headShotImg {
    position: absoulute;
    left: 0;
    height: -webkit-fill-available;
    max-height: 100vh;
    border: none;
    border-left: 3px solid #486781;
  }
  #headShotCol {
    position: unset;
  }
  #column2 {
    position: relative;
    top: 0px;
    left: 0px;
    width: unset;
    height: unset;
  }
  #greetingLine1 {
    margin-top: 50px;
    padding-left: 12%;
  }
  #greetingLine2 {
    font-size: 18px;
    padding-left: 15%;
  }
  #banner {
    position: relative;
    left: -15%;
    width: 115%;
    margin: 20px;
    padding: 20px 0 20px 15px;
  }
}

@media (min-width: 992px) {
  #greetingLine1 {
    padding-left: 15%;
  }
  #greetingLine2 {
    font-size: 28px;
    padding-left: 20%;
  }
  #greetingLine3 {
    font-size: 40px;
  }
  #greetingLine1,
  #greetingLine4 {
    font-size: 30px;
  }
  #banner {
    left: -8%;
    width: 100%;
    padding: 20px 20px 20px 40px;
  }
  #bannerRow1 {
    font-size: 22px;
  }
  #bannerRow2 {
    font-size: 26px;
    letter-spacing: 6px;
  }
  #btnResume,
  #finalLine {
    font-size: 22px;
  }
}
@media (min-width: 1200px) {
  #greetingLine3 {
    font-size: 50px;
  }
  #banner {
    left: -10%;
  }
  #bannerRow1 {
    font-size: 30px;
  }
  #bannerRow2 {
    font-size: 36px;
  }
  .haywardContainer {
    padding-right: 25%;
  }
  #btnResume {
    font-size: 28px;
  }
  #finalLine {
    font-size: 30px;
  }
}
@media (min-width: 1400px) {
  #greetingLine1,
  #greetingLine4 {
    font-size: 40px;
  }
  #greetingLine2 {
    font-size: 38px;
  }
  #greetingLine3 {
    font-size: 60px;
  }
  #banner {
    left: -5%;
    width: 100%;
  }
  #finalLine {
    padding-right: 15%;
    font-size: 30px;
  }
}

